import './App.css';
import Navigation from './Components/Navigation';
import Logo from './Components/Logo';
import ImageLinkForm from './Components/ImageLinkForm';
import { Component } from 'react';
import Rank from './Components/Rank';
import FaceRecognition from './Components/FaceRecognition';
import Signin from './Components/Signin';
import Register from './Components/Register';



class App extends Component {
  constructor() {
    super();
    this.state = {
      input: '',
      imageURL: '',
      route: 'signin',
      isSignedin: false,
      box: {},
      user: {
        id: '',
        name: "",
        email: "",
        password: "",
        entries: 0
      }

    }
  }

  calculateFacelocation = (data) => {
    const image = document.getElementById('inputimage')
    const width = Number(image.width) //calculate image width and height
    const height = Number(image.height)
    return {
      leftcol: data.left_col * width,
      topRow: data.top_row * height,
      rightCol: width - (data.right_col * width),
      bottomRow: height - (data.bottom_row * height)
    }
  }


  displayFacebox = (box) => {
    this.setState({ box: box })   //we can change this to with ES6 ({box})
    // console.log(this.state.box)
  }


  loadUser = (loadedUser) => {
    this.setState({
      user: {
        id: loadedUser.id,
        name: loadedUser.name,
        email: loadedUser.email,
        password: loadedUser.password,
        entries: loadedUser.entries
      }
    })
    // console.log('loadeduser is: ', loadedUser)
  }


  onChangeInput = (event) => {
    this.setState({ input: event.target.value })

  }

  onClicked = () => {
    this.setState({ imageURL: this.state.input })
    
    fetch('https://polar-basin-69765.herokuapp.com/image', {

      method: 'post',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(
        {
          imageURL: `${this.state.imageURL}`
        }
      )

    }).then(res => res.json())
      .then(
        data => {
          
          //API call for Entries this will increment the entries count in database
          fetch('https://polar-basin-69765.herokuapp.com/rank', {
            method: 'put',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(
              {
                id: `${this.state.user.id}`
              })  
            })
            .then(res => res.json()).then(() => {
              var entries = parseInt((this.state.user.entries));
              this.setState(Object.assign(this.state.user, {entries: (entries+1)}))
              // Updating Entries in the User State also
            })
            
            this.displayFacebox(this.calculateFacelocation(data))
            //as displayFacebox() function needs a box object which our calculateFaceLocation function will return

      }).catch(err => {
          window.alert('Please Try Again, API ERROR')
      })





  }

  onRouteChange = (route) => {

    if (route == 'home') {
      this.setState({ isSignedin: true });
    }
    else {
      this.setState({ isSignedin: false });
      this.setState({imageURL: ''})
    }
    this.setState({ route: route })

  }

  

  render() {
    if (this.state.route === 'home') {
      return (
        <div className="App">
          <Navigation isSignedin={this.state.isSignedin} onRouteChange={this.onRouteChange} />
          <Logo />
          <Rank name={this.state.user.name} Rank={this.state.user.entries} />
          <ImageLinkForm buttonClicked={this.onClicked} searchChange={this.onChangeInput} />
          <FaceRecognition box={this.state.box} imageURL={this.state.imageURL} />
        </div>
      )
    }
    else if (this.state.route == 'signin') {
      return (
        <div className="App">
          <Navigation isSignedin={this.state.isSignedin} onRouteChange={this.onRouteChange} />
          <Signin loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
        </div>
      )
    }
    else {
      return (
        <div className="App">
          <Navigation isSignedin={this.state.isSignedin} onRouteChange={this.onRouteChange} />
          <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
        </div>
      )
    }
  }

}

export default App;
